<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="show_Loader" />
      <!-- Loader -->
      <div
        class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
        v-show="show_Loader"
      >
        <div class="text-primary">
          <i class="fas fa-spinner fa-pulse fa-5x"></i>
        </div>
      </div>
      <!--Loader-->
      <div style="background: #fff;">
        <div class="container p-2 flexbox align-items-center justify-between mobile-p-1">
          <div class="flexbox align-items-center p-t-1 p-b-1">
            <img src="@/assets/icons/passanger-icon.svg" alt="Passaner Icon" style="width: 25px;" />
            <h2 class="title mr-l-1 font-semibold font-h-1 mobile-font-sm">Payment Options</h2>
          </div>
          <div>
            <h1 class="title mobile-font-sm">
              <span class="mr-r-sm display-inline-block">Total:</span>
              <span class="display-inline-block">
                <em class="label-currency font-bold">KES</em>
                <em
                  class="label-amount font-bold"
                >{{ (total_amount + return_total_amount) | formatCurrency }}</em>
              </span>
            </h1>
          </div>
        </div>
      </div>

      <div class="line-divider">&nbsp;</div>

      <div class="container p-3 mobile-p-1 bg-white mr-b-2">
        <div class="flexbox justify-between mobile-flex-wrap">
          <!-- payment_status == 2 -->

          <div class="w-50-gap mobile-w-100 text-center" v-show="payment_status == 2">
            <h2 class="title mr-b-1">You have Successfully booked your ticket.</h2>
            <img
              src="@/assets/icons/booked.png"
              alt="Passaner Icon"
              style="width: 40%;"
              class="mr-auto"
            />
            <div class="card p-1 mr-b-1 mr-t-1">
              <h4
                class="mr-b-sm bold title"
              >Amount KES {{ (total_amount + return_total_amount) | formatCurrency }}</h4>
              <p>Your ticket &amp; booking details will be sent via SMS</p>
            </div>
            <div>&nbsp;</div>
            <div class="card p-1">
              <h4 class="mr-b-sm bold title">Need Help?</h4>
              <p>Have queries regarding your booking?</p>
              <p class="mr-b-1">Contact our tean.</p>

              <a href="tel:0709897000">Call us now +254 709 897 000</a>
            </div>
          </div>

          <div class="w-50-gap mobile-w-100" style="display:none" v-show="payment_status == 3">
            <h2 class="title mr-b-1">MPESA STK push not successful.</h2>

            <ul class="mr-b-1 mobile-mr-l-1 p-l-2">
              <li class="text color-dark mr-b-sm">Go to the MPESA menu on your phone.</li>
              <li
                class="text color-dark mr-b-sm"
              >Select PayBill option. Enter Business Number: 525600</li>
              <li
                class="text color-dark mr-b-sm"
              >You will get a receipt from MPESA and an SMS confirmation from MODERN COAST.</li>
              <li
                class="text color-dark mr-b-sm"
              >Enter {{ trip_info.payment.payment_unique_number }} as the Account Number</li>
              <li
                class="text color-dark mr-b-sm"
              >Enter the EXACT Amount (KSH. {{ (total_amount + return_total_amount) | formatCurrency }})</li>
              <li class="text color-dark mr-b-sm">Complete your transaction on your phone.</li>
              <li
                class="text color-dark mr-b-sm"
              >You will receive a transaction confirmation SMS from MPESA.</li>
            </ul>
          </div>

          <div class="w-50-gap mobile-w-100" v-show="payment_status == 0">
            <h2 class="title mr-b-1">Payment Instructions</h2>

            <ul class="mr-b-1 mobile-mr-l-1 p-l-2">
              <li class="text color-dark mr-b-sm">Payment request will be pushed to your phone.</li>
              <li
                class="text color-dark mr-b-sm"
              >Enter your MPESA PIN on your phone to complete payment.</li>
              <li
                class="text color-dark mr-b-sm"
              >You will get a receipt from MPESA and an SMS confirmation from MODERN COAST.</li>
              <li
                class="text color-dark mr-b-sm"
              >Didn't get the prompt on your phone? Kindly dial *234*1*6# to force SIM update. For SIM cards MORE THAN 2 YEARS OLD a SIM swap may be necessary.</li>
            </ul>

            <p class="text color-dark font-h-1 mr-b-1">
              To complete the payment you will receive a prompt from MPESA
              on the mobile number below
            </p>

            <form @submit.prevent="getStkPush">
              <div class="form-group w-100">
                <label class="font-h-1">Phone Number</label>
                <div class="input-pair">
                  <select
                    class="form-control country-code selector"
                    v-model="passenger_details[0].passenger_phone_code"
                  >
                    <option
                      :value="country.country_phonecode"
                      v-for="(country, index) in bus_seats.countries"
                      :key="index"
                    >{{ country.country_name_phonecode }}</option>
                  </select>
                  <input
                    type="tel"
                    name="p_phone[]"
                    placeholder="Phone Number"
                    required
                    class="form-control"
                    v-model="passenger_details[0].passenger_phone_number"
                    @blur="validatePhoneNumber(passenger_details[0].passenger_phone_number)"
                    @keyup.enter="validatePhoneNumber(passenger_details[0].passenger_phone_number)"
                  />
                </div>
              </div>
              <!-- Show form feedback here -->
              <div
                class="form-group form-message hidden"
              >Enter some form feedback here or error message</div>
              <!-- Show form feedback here -->
              <div class="form-group text-center mr-p-t-sm -p-b-sm">
                <button type="submit" class="btn">SUBMIT</button>
              </div>
            </form>
          </div>

          <div class="w-50-gap mobile-w-100 mobile-mr-t-2">
            <h2 class="title mr-b-1">Trip Details</h2>

            <div class="card with-dropdown p-2 mobile-p-1">
              <a
                href="#"
                @click.prevent="toogleTripDetails"
                class="flexbox justify-between align-items-center"
              >
                <span class="title font-h-1 font-light color-dark">Ongoing Trip</span>
                <img src="@/assets/icons/caret-down.svg" alt="Passaner Icon" style="width: 20px;" />
              </a>
              <div class="content p-t-2" v-show="showTripDetails">
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Bus</span>
                  <span>{{ trip.selected_bus.route_code }}</span>
                </div>
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Booking Date</span>
                  <span>{{ trip.travelling_on | formatDate }}</span>
                </div>
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Departure Time</span>
                  <span>{{ trip.selected_bus.latest_departure_time }}</span>
                </div>
              </div>
            </div>

            <div
              class="card mr-t-2 with-dropdown p-2 mobile-p-1"
              v-if="return_selected_seats.length > 0"
            >
              <a
                href="#"
                @click.prevent="toogleTripDetails"
                class="flexbox justify-between align-items-center"
              >
                <span class="title font-h-1 font-light color-dark">Returning Trip</span>
                <img src="@/assets/icons/caret-down.svg" alt="Passaner Icon" style="width: 20px;" />
              </a>
              <div class="content p-t-2" v-show="showTripDetails">
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Bus</span>
                  <span>{{ trip.return_selected_bus.route_code }}</span>
                </div>
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Booking Date</span>
                  <span>{{ trip.returning_on | formatDate }}</span>
                </div>
                <div class="flexbox align-items-center justify-between mr-b-sm">
                  <span>Departure Time</span>
                  <span>{{ trip.return_selected_bus.latest_departure_time }}</span>
                </div>
              </div>
            </div>

            <div class="card mr-t-2 mr-b-1">
              <div
                class="row header flexbox align-items-center justify-between p-b-sm p-t-2 p-r-2 p-l-2 mobile-p-1"
              >
                <span class="title color-dark font-h-1">Price Breakdown</span>
                <span class="title color-dark font-h-1">Summary</span>
              </div>
              <div
                v-for="(selected_seat, index) in selected_seats"
                :key="index"
                class="row flexbox align-items-center justify-between p-t-sm p-b-1 p-r-2 p-l-2 mobile-p-1"
              >
                <span class="font-h-1">Onward Fare</span>
                <span class="font-h-1">KES {{ total_amount | formatCurrency }}</span>
              </div>

              <div
                v-for="return_selected_seat in return_selected_seats"
                :key="return_selected_seat.seat_number"
                class="row flexbox align-items-center justify-between p-t-sm p-b-1 p-r-2 p-l-2 mobile-p-1"
              >
                <span class="font-h-1">Returning Fare</span>
                <span class="font-h-1">KES {{ return_total_amount | formatCurrency }}</span>
              </div>

              <div
                class="footer flexbox align-items-center justify-between bg-secondary p-2 mobile-p-1"
              >
                <h1 class="text-white font-bold font-h-2">Total Amount</h1>
                <h1 class="text-white font-bold font-h-2">
                  <em class="label-currency">KES</em>
                  <em
                    class="label-amount"
                  >{{ (total_amount + return_total_amount) | formatCurrency }}</em>
                </h1>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="container p-3 mobile-p-2 bg-white">
        <div class="navigation-wrapper flexbox justify-between align-items-center">
          <router-link to="/Passangers" class="nav-prev flexbox justify-between align-items-center">
            <img
              src="@/assets/icons/arrow-left-icon.svg"
              alt="Arrow"
              class="mr-r-1"
              style="width: 30px"
            />
            <span class="color-primary mobile-hidden">BACK</span>
          </router-link>
          <a href="#" class="nav-next flexbox justify-between align-items-center hidden">
            <span class="color-primary mobile-hidden">NEXT</span>
            <img
              src="@/assets/icons/arrow-left-icon.svg"
              alt="Arrow"
              class="mr-l-1 rotated-arrow"
              style="width: 30px"
            />
          </a>
        </div>
      </div>
    </section>

    <!-- App Footer -->
    <AppFooter />
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

export default {
  name: "AppPayment",
  components: {
    AppHeader,
    AppLoader,
    AppFooter
  },
  computed: mapGetters([
    "trip",
    "selected_seats",
    "passenger_details",
    "total_amount",
    "bus_seats",
    "show_Loader",
    "return_selected_seats",
    "return_bus_seats",
    "return_total_amount",
    "payment_status",
    "trip_info"
  ]),
  data() {
    return {
      showTripDetails: false
    };
  },
  methods: {
    ...mapActions(["getStkPush", "showWarning"]),
    toogleTripDetails() {
      this.showTripDetails = !this.showTripDetails;
    },
    validatePhoneNumber(phone_number) {
      //Regex to validate Phone Number
      let to_match = /^(?:254|\+254|0)?(7(?:(?:[0-9][0-9])|(?:0[0-8])|(4[0-1]))[0-9]{6})$/;
      let check_validity = to_match.test(phone_number);
      if (!check_validity) {
        //Alert
        this.showWarning("Incorrect phone number format");
      }
    }
  },
  mounted() {
    //scroll to top
    window.scrollTo(0, 0);
  }
};
</script>


